<template>
    <div class="page">
        <div class="info-title">
            <div>
                <h3>我的头像</h3>
                <p>请上传身穿白大褂真实照片作为头像</p>
            </div>
                <img @click="upImage" :src="item.memberIcon? item.memberIcon :
            'https://uniondrug-release.oss-cn-shanghai.aliyuncs.com/backend.app/2h80j6hdfu3o35tb3evlj9f3as.png'"
                     alt="">

        </div>
        <div class="list">
            <div class="item">
                <p>真实姓名</p>
                 <p class="p">{{ item.memberName }}</p>
            </div>
            <div class="item">
                <p>性别</p>
                <van-radio-group class="title title2" disabled v-model="item.sex" direction="horizontal">
                    <van-radio checked-color="#00B095" name="1">男</van-radio>
                    <van-radio checked-color="#00B095" name="2">女</van-radio>
                </van-radio-group>
            </div>
            <div class="item" @click="UDToRealNameAuthentication">
                <p>实名认证</p>
                <div>
                    <p v-if="item.isCertification == '0'">未认证</p>
                    <p v-if="item.isCertification == '1'">已认证</p>
                    <van-icon name="arrow"/>
                </div>
            </div>

            <div class="item" @click="UDToPrivilegeAttestation">
                <p>药师认证</p>
                <div>
                    <p>{{ isCertificationFn(item.pharmacistStatus) }}</p>
                    <van-icon name="arrow"/>
                </div>
            </div>

            <div class="item">
                <p>头衔</p>
                <input v-model="item.memberTitle" placeholder="请输入头衔" type="text">
            </div>
            <div class="item">
                <p>任职单位</p>
                <input v-model="item.workPlace" placeholder="请输入任职单位" type="text">
            </div>
            <div class="item">
                <p>个人简介</p>
                <input v-model="item.personProfile" placeholder="请输入个人简介" type="text">
            </div>
            <div class="item" @click="qianList()">
                <p>授课协议</p>
                <div>
                    <p>{{ item.protocolStatus == '0' ? '未签署' : '已签署'}}</p>
                    <van-icon name="arrow"/>
                </div>
            </div>
        </div>

        <div class="footer">
            <button class="btn" @click="getData">确认</button>
        </div>
    </div>
</template>

<script>
    export default {
        name: "inofUp",
        data() {
            return {
                item: {}
            }
        },
        created() {
            this.info();
        },
        methods: {
            isCertificationFn(pharmacistStatus){
                if(pharmacistStatus == '0'){
                    return '未认证'
                }
                if(pharmacistStatus == '1'){
                    return '待审核'
                }
                if(pharmacistStatus == '2'){
                    return '已认证'
                }
                if(pharmacistStatus == '3'){
                    return '认证失败'
                }
            },
            UDToPrivilegeAttestation(){
                sessionStorage.removeItem('login')
                this.$bs.UDToPrivilegeAttestation({}).then(() => {

                })
            },
            qianList(){
                this.$router.push('qianList')
            },
            upImage(){
                this.$bs.upImage({type: '1'}).then((e) => {
                    console.log(e);
                    if (e.status == '1') {
                        this.$toast('上传成功')
                        this.item.memberIcon = e.data.data
                    } else if (e.msg) {
                        this.$toast(e.msg)
                    }

                })
            },
            afterRead(file) {
                // 此时可以自行将文件上传至服务器
                console.log(file);
                var formData = new FormData(); //构造一个 FormData，把后台需要发送的参数添加
                formData.append('file', file.file); //接口需要传的参数
                this.$api.upload(formData).then(res => {
                    this.item.memberIcon = res.url;
                })
            },
            UDToRealNameAuthentication(){
                sessionStorage.removeItem('login')
                this.$bs.UDToRealNameAuthentication({}).then(()=>{

                })
            },
            info() {
                this.$api.info().then((res) => {
                    this.item = res;
                })
            },
            getData(){
                this.$api.updateInfo(this.item).then((res) => {
                    history.back();
                })
            }
        },
    }
</script>

